.horizontal-scroll{
    margin-top: -40px;
    overflow: auto; padding: 5px;
    white-space: nowrap;
  }
  .abs-cross-button{
    width: 40px;
    height: 40px;
    color:#222;position: absolute;z-index: 9999;
    top: 10px;
    right: 10px;;
    box-shadow: 0 7px 20px 0 rgba(1,1,1,0.04);
    background:#fff !important;
    border-radius: 50%;
    ;
  }
  .abs-toggle-button{
position: absolute;z-index: 9999;
    top: 5px;
    left: -20px;;
   
    ;
