.switchmute {
    position: relative;padding:5px;padding-left:0px;
    display: inline-block;
    width: 90px;
    transform: scale(0.6);
    height: 14px;
    margin: 10px;
  }
  
  .switchmute input {display:none;}
  
  .sliderlfmute {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #aaa;box-shadow:0 7px 20px 0 rgba(48,25,131,0.3);
    -webkit-transition: .4s;
    transition: .4s;
  }
  .sliderlfmute:before {
    position: absolute;
    content: "";
    height: 50px;
    width: 50px;
    left: 0px;
    bottom: -18px;
    background: #99cc99;box-shadow:0 7px 20px 0 rgba(48,25,131,0.03);;border:7px solid #f9f9f9;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .sliderlfmute {
     background: #f9f9f9;box-shadow:0 7px 20px 0 rgba(48,25,131,0.3);
  }
  
  input:focus + .sliderlfmute {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .sliderlfmute:before {
    -webkit-transform: translateX(40px);
    -ms-transform: translateX(40px);
    transform: translateX(40px);
  }
  
  /* Rounded sliderlfmutes */
  .sliderlfmute.round {
    border-radius: 34px;
  }
  
  .sliderlfmute.round:before {
    border-radius: 50%;
  }
  
  .mutetitle{ position:relative; padding:5px;font-size:18px;top:-7px; font-weight:bold; }
  
  